.project-form {
  display: flex;
  flex-flow: column nowrap;
  max-width: 1180px;
  min-height: 506px;
  // transition: all 0.8s ease 0s;
  align-items: flex-start;
  padding: 65px 150px;
  gap: 10px;
  background: #1f384f;
  border-radius: 15px;

  @media (max-width: 1280px) {
    padding: 30px;
    align-items: center;
    min-height: 433px;
  }

  &__title {
    width: 100%;
    font-size: 32px;
    font-weight: 600;
    line-height: 39.14px;
    text-align: center;
    color: white;
    text-transform: uppercase;
    margin-bottom: 5px;

    @media (max-width: 900px) {
      font-family: Commissioner;
      font-size: 22px;
      font-weight: 600;
      line-height: 26.91px;
    }
  }
  &__subtitle {
    width: 100%;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.57px;
    text-align: center;
    margin-bottom: 17px;
    color: white;

    @media (max-width: 900px) {
      max-width: 350px;
    }
  }

  &__inputs {
    display: flex;
    flex-flow: row nowrap;
    gap: 20px;
    margin-bottom: 8px;

    @media (max-width: 900px) {
      gap: 10px;
    }

    @media (max-width: 600px) {
      flex-flow: column nowrap;
      gap: 10px;
      width: 100%;
      margin-bottom: 0px;
    }
  }

  &__input {
    width: 280px;
    height: 56px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 6px;
    padding: 16px;
    color: white;

    @media (max-width: 1280px) {
      max-width: 280px;
      width: 100%;
    }
    @media (max-width: 600px) {
      max-width: 100%;
      width: 100%;
    }

    &_tel {
      @media (max-width: 900px) {
        // max-width: 186px;
        flex: 1 1 186px;

        // max-width: 280px;
        // width: 100%;
      }
      @media (max-width: 600px) {
        flex: none;
      }
    }
    &_email {
      @media (max-width: 900px) {
        // max-width: 219px;
        flex: 1 1 219px;
        // max-width: 280px;
        // width: 100%;
      }
      @media (max-width: 600px) {
        flex: none;
      }
    }
    &_name {
      @media (max-width: 900px) {
        // max-width: 219px;
        flex: 1 1 210px;
        // max-width: 280px;
        // width: 100%;
      }
      @media (max-width: 600px) {
        flex: auto;
      }
    }

    &.project-form__input_textarea {
      width: unset;
      height: unset;
      max-width: 880px;
      min-height: 137px;
      margin-bottom: 10px;
      @media (max-width: 1280px) {
        max-width: 707px;
        width: 100%;
      }
      @media (max-width: 900px) {
        max-width: 687px;
        margin-bottom: 0;
      }

      @media (max-width: 600px) {
        min-height: 112px;
        height: 112px;
      }
    }

    &:focus-visible {
      outline: none;
      border: none;
      border-bottom: 1px solid var(--color-black);
    }

    &::placeholder {
      font-size: 16px;
      font-weight: 400;
      line-height: 19.57px;
      text-align: left;
      color: rgba(white, 0.7);
    }
  }

  &__label {
    font-family: Montserrat;
    font-size: 20px;
    line-height: 110%;
    display: flex;
    flex-flow: column nowrap;
    gap: 24px;
  }

  &__submit-wrap {
    display: flex;
    flex-flow: row nowrap;
    gap: 20px;

    @media (max-width: 1280px) {
      max-width: 707px;
    }

    @media (max-width: 900px) {
      max-width: 687px;
      width: 100%;

      align-self: center;
    }

    @media (max-width: 600px) {
      flex-flow: column nowrap;
      width: 100%;
      gap: 13px;
    }

    .call-form__label {
      .checkbox__input + span {
        top: 2px;
      }

      @media (max-width: 900px) {
        max-width: 300px;
      }
    }

    .project-form__submit {
      width: 280px;
      height: 48px;
      background-color: transparent;
      border: 1px solid #ffffff;
      border-radius: 6px;
      color: white;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      transition: background-color 0.3s ease-in-out,
        border-color 0.3s ease-in-out;
      @media (max-width: 900px) {
        width: 211px;
      }

      @media (max-width: 600px) {
        width: 100%;
      }

      &:hover {
        background-color: $color-2FA7BE;
        border-color: $color-2FA7BE;
      }
      &:active {
        background-color: $color-268AB1;
        border-color: $color-268AB1;
      }
    }
  }
}

.checkbox {
  position: relative;
  display: block;
  padding-left: 36px;
  cursor: pointer;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.8);

  &__link {
    color: rgba(255, 255, 255, 0.8);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: white;
      text-decoration: underline;
    }
  }

  &__input {
    display: none;

    & + span {
      position: absolute;
      top: -2px;
      left: 0;
      text-align: center;
      width: 23px;
      height: 23px;
      border: 1px solid #ffffff;
      border-radius: 2px;
      // transition: all 0.3s ease-in-out;

      &::before {
        content: "";
        width: 18px;
        height: 18px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        background-image: url(../img/check-mark-white.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        opacity: 0;
        // transition: all 0.3s ease-in-out;
      }
    }

    &:checked + span::before {
      opacity: 1;
    }
  }
}

