.form {
  // box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 10% 17% 40% 20% 9%;
  gap: 1%;
  // justify-content: center;
  // flex-direction: column;
  align-items: start;
  justify-content: start;
  padding: 50px;
  // gap: 60px;

  // width: 544px;
  // height: 672px;

  background: #ffffff;
  border-radius: 10px;

  @media (max-width: 900px) {
    padding: 30px;
    grid-template-rows: 8% 11% 49% 18% 6%;
