    grid-template-rows: 6% 13% 47% 15% 13%;
    padding: 20px;
  }

  .form__title {
    font-family: Inter;
    font-size: 40px;
    font-weight: 400;
    line-height: 48.41px;
    text-align: left;

    @media (max-width: 900px) {
      font-family: Inter;
      font-size: 30px;
      font-weight: 400;
      line-height: 36.31px;
      text-align: left;
    }

    @media (max-width: 660px) {
      font-size: 22px;
      line-height: 27.31px;
    }
  }
  .form__desc {
    max-width: 75%;
    font-family: Inter;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;

    @media (max-width: 660px) {
      font-size: 14px;
      max-width: 100%;
    }
  }

  .form__label-block {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    gap: 20px;

    @media (max-width: 900px) {
      gap: 30px;
    }

    @media (max-width: 660px) {
      // gap: 20px;
    }

    .form__label {
      display: grid;
      grid-template-columns: max-content 1fr;
      grid-template-rows: 1fr 2fr;
      justify-items: start;
      align-items: start;
      gap: 5px 0;

      font-family: Inter;
      font-size: 14px;
      font-weight: 400;

      border-bottom: 1px solid #a9a9a9;

      .form__label-text {
        grid-row: 1/1;
        color: #bababa;
      }

      .form__label-star {
        grid-row: 1/1;
        color: #363636;
      }

      .form__input {
        grid-column: span 2;
        width: 100%;
        line-height: 37px;
        &::placeholder {
          font-size: 16px;
          color: #363636;
        }
      }
    }
  }

  .form__btn {
    align-self: end;
    width: 100%;
    height: 60px;

    @media (max-width: 900px) {
      font-size: 15px;
    }
    @media (max-width: 660px) {
      height: 46px;
    }
  }

  .form__agreement-wrap {
    align-self: end;

    font-family: Inter;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    text-align: left;

    @media (max-width: 660px) {
      font-size: 13px;
    }

    .form__agreement-text {
      color: #a9a9a9;
    }
    .form__agreement-link {
      color: #363636;
    }
  }
}

