  .form {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 14% 58% 12% 4%;
    gap: 4%;
    align-items: start;
    justify-content: start;
    position: relative;
    // padding: 50px;
    // background: #ffffff;
    // border-radius: 10px;

    /* Форма заявки при запросе цены и планировки */

    /* Auto layout */
    // display: flex;
    // flex-direction: column;
    // align-items: flex-start;
    padding: 50px;
    // gap: 20px;
    // isolation: isolate;

    // position: relative;
    max-width: 495px;
    width: 100%;
    min-height: 827px;

    background: #ffffff;
    border-radius: 10px;

    .form__title {
      font-family: Inter;
      font-size: 40px;
      font-weight: 400;
      line-height: 48.41px;
      text-align: left;
      max-width: 250px;
    }
    .form__desc {
      max-width: 75%;
      font-family: Inter;
      font-size: 16px;
      font-weight: 400;
      line-height: 22.4px;
      text-align: left;
    }

    .form__label-block {
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: repeat(3, 1fr);
      gap: 30px;

      .form__label {
        display: grid;
        grid-template-columns: max-content 1fr;
        grid-template-rows: 1fr 2fr;
        justify-items: start;
        align-items: start;
        gap: 5px 0;

        font-family: Inter;
        font-size: 14px;
        font-weight: 400;

        border-bottom: 1px solid #a9a9a9;

        .form__label-text {
          grid-row: 1/1;
          color: #bababa;
        }

        .form__label-star {
          grid-row: 1/1;
          color: #363636;
        }

        .form__input {
          grid-column: span 2;
          width: 100%;
          line-height: 37px;

          &::placeholder {
            font-size: 16px;
            color: #363636;
          }
        }

        &.form__label_textarea {
          grid-template-columns: max-content 1fr;
          grid-template-rows: max-content 1fr;

          .form__input_textarea {
            max-width: 395px;
            min-height: 116px;
          }
        }
      }
    }

    .form__btn {
      align-self: end;
      width: 100%;
      height: 60px;
    }

    .form__close-btn {
      position: absolute;
      width: 20px;
      height: 15px;
      right: 20px;
      top: 20px;
      cursor: pointer;

      &::after {
        content: "";
        position: absolute;
        inset: -12px;
      }

      // border: 1.5px solid #8C8C8C;
      // flex: none;
      // order: 2;
      // flex-grow: 0;
      // z-index: 2;
    }

