.not-found {
  .not-found {
    background: $color-F3FAFC;
  }
  .not-found__container {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 186px 10px 148px;
    border-bottom: 1px solid var(--color-E9E9E9);

    .not-found__img {
      margin-bottom: 20px;

      // width: 500px;
      // height: 300px;
      // background: url(../img/404.png);
      // mix-blend-mode: hard-light;
      // transform: matrix(0, -1, -1, 0, 0, 0);
      & img {
        mix-blend-mode: hard-light;
      }
    }

    .not-found__title {
      font-size: 40px;
      font-weight: 500;
      line-height: 48.92px;
      text-align: center;
      color: $color-555A5E;
      text-transform: uppercase;
      // margin-bottom: 100px;
    }

    .not-found__text {
      font-size: 22px;
      font-weight: 400;
      line-height: 26.91px;
      text-align: center;
      color: $color-555A5E;
    }

    .not-found__btns-wrapper {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      gap: 20px;
    }
    .btn {
      width: 174px;
      border-radius: 6px;
    }
  }
}

