// --------------------------------------- CATALOG start ---------------------------------------
.section_catalog {
  .catalog-main__container {
    position: relative;

    @media (max-width: 1280px) {
      padding: 0 30px;
    }

    // padding: 0 10px;
    // min-height: 100vh;
    .link {
      position: absolute;
      right: 10px;
      top: 126px;
      text-align: right;
      @media (max-width: 1280px) {
        position: relative;
        top: unset;
        right: unset;
        display: block;
        // width: 300px;
        // margin: 0 auto;
        text-align: center;
      }
    }
  }

  .card {
    &:hover .card__text-block {
      // backdrop-filter: blur(10px);
      padding: 0;
      width: 100%;
      height: 100%;
      // max-width: 100%;
      // min-height: 100%;
    }
  }

  .catalog-main__head {
    position: relative;
    padding-top: 98px;
    margin-bottom: 28px;

    @media (max-width: 660px) {
      padding-top: 100px;
      margin-bottom: 35px;
    }
  }
  .catalog-main__body {
    padding: 0 10px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;
    gap: 20px 10px;

    @media (max-width: 1280px) {
      flex-flow: column wrap;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: 43px;
      padding: 0;
    }
    .catalog-main__card {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      @media (max-width: 900px) {
        width: 100%;
      }
      @media (max-width: 660px) {
        min-height: 234px;
      }
    }
  }
  .card_tile {
    background-image: url("../img/catalog-tile.jpg");
  }
  .card_crumb {
    background-image: url("../img/catalog-crumb.jpg");
  }

  .card_roll {
    background-image: url("../img/catalog-roll.jpg");
  }
  .card_seamless {
    background-image: url("../img/catalog-seamless.jpg");
  }
}

