      position: relative;
      padding-top: 101px;
      margin-bottom: 28px;
    }
    .news-main__body {
      padding: 0 10px;
      display: flex;
      flex-flow: row wrap;
      align-items: center;
      justify-content: space-between;
      gap: 20px 2%;

      @media (max-width: 1280px) {
        padding: 0;
      }
      @media (max-width: 750px) {
        flex-flow: column wrap;
      }

      .card {
        flex: 1 1 49%;
        // width: auto;
        // max-width: 49%;
        width: 100%;

        @media (max-width: 1280px) {
          padding: 27px 34px;
