// --------------------------------------- PORTFOLIO start ---------------------------------------

.portfolio {
  .container_portfolio {
    padding: 101px 0 120px 0;
    display: grid;
    flex-flow: column nowrap;
    align-items: center;
    // justify-content: space-between;
    gap: 3px;

    @media (max-width: 1280px) {
      padding: 101px 30px 120px;
    }

    @media (max-width: 700px) {
      padding: 130px 15px 70px;
    }

    .main-block__title {
      margin-bottom: 25px;
      @media (max-width: 700px) {
        margin-bottom: 2px;
      }
    }

    .main-block__body {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(6, 341px);
      gap: 20px;
      padding: 0 10px;

      @media (max-width: 1280px) {
        padding: 0;
      }

      @media (max-width: 900px) {
        grid-template-rows: repeat(6, 237px);
      }

      @media (max-width: 700px) {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(9, 237px);
        gap: 15px;
      }
    }

