      gap: 103px 8%;
    }

    @media (max-width: 750px) {
      grid-template-rows: 2fr 1fr;

      padding: 70px 15px 77px;
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: auto auto auto;
      gap: 30px 8%;
    }
    // padding: 0 10px;
    // margin-bottom: 105px;
    .goods-item__img-box {
      // width: 580px;
      display: flex;
      flex-flow: column nowrap;
      // align-items: center;
      justify-content: center;
      gap: 35px;

      @media (max-width: 1280px) {
        gap: 21px;
      }

      @media (max-width: 750px) {
        gap: 15px;
      }

      .goods-item__img {
        width: 380px;
        height: 380px;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
        border-radius: 15px;

        @media (max-width: 1280px) {
          height: 275px;
          width: 100%;
        }

        @media (max-width: 750px) {
          height: 337px;
        }

        & img {
          width: 65%;
          height: 65%;
          object-fit: contain;
          // object-position: center;
          display: block;
          transition: transform 0.3s ease-in-out;
          @media (max-width: 1280px) {
            height: 100%;
            width: 100%;
          }
          &:hover {
            transform: scale(1.05);
          }
        }
      }
      .small-img-wrap {
        display: flex;

        flex-flow: row nowrap;
        align-items: stretch;
        justify-content: flex-start;
        gap: 18px;
        width: 100%;

        overflow-x: auto;
        max-width: 380px;

        @media (max-width: 1280px) {
          max-width: 100%;
        }

        @media (max-width: 750px) {
          gap: 14px;
        }
      }
      .small-img-wrap__img {
        flex: none;
        width: 82px;
        height: 82px;
        border-radius: 10px;
        overflow: hidden;
        cursor: pointer;
        border: 1px solid transparent;
        transition: border 0.3s ease-in-out;

        @media (max-width: 1280px) {
          width: 70px;
          height: 71px;
        }

        @media (max-width: 750px) {
          width: 62px;
          height: 62px;
        }

        & svg {
          @media (max-width: 1280px) {
            width: 68px;
            height: 69px;
          }
          @media (max-width: 750px) {
            width: 60px;
            height: 60px;
          }
        }

        &:hover {
          border: 1px solid $color-1F71A2;
        }

        & img {
          width: 100%;
          height: 100%;
          display: block;
          object-fit: cover;

          // transition: transform 0.1s ease-in-out;
          // &:hover {
          //   transform: scale(1.05);
          // }
        }

        .news-card {
        }
        .news-card__date {
        }
        .news-card__title {
        }
        .news-card__desc {
        }
      }
    }
    .text-block {
      width: 400px;
      display: flex;
      flex-flow: column nowrap;
      align-items: flex-start;
      justify-content: flex-start;
      gap: 20px;

      @media (max-width: 1280px) {
        width: 100%;
        position: relative;
      }

      @media (max-width: 750px) {
        gap: 19px;
      }

      .text-block__title {
        margin-top: 15px;
        font-size: 32px;
        font-weight: 600;
        line-height: 39.14px;
        text-align: left;
        color: $color-555A5E;
        text-transform: uppercase;

        @media (max-width: 1280px) {
          font-family: Commissioner;
          font-size: 20px;
          font-weight: 600;
          line-height: 24.46px;
          text-align: left;
          margin-top: 0;
        }
      }

      .color-list {
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: center;
        gap: 13px;

        .color-list__color-item {
          position: relative;

          width: 30px;
          height: 30px;
          border-radius: 50%;
          border: 1px solid #989898;
          cursor: pointer;

          .color-list__color-small-box {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            width: 30px;
            height: 30px;
            border-radius: 50%;

            &_1 {
              background: #00c27c;
            }
            &_2 {
              background: #7d471f;
            }
            &_3 {
              background: #c6c6c6;
            }
            &_4 {
              background: #1934c2;
            }
            &_5 {
              background: #fcbe15;
            }
            &_6 {
              background: #d10000;
            }
            &_7 {
              background: #000000;
            }
          }

          &_active {
            // width: 15px;
            // height: 15px;

            border: 1px solid #989898;
            // offset: 2px;

            .color-list__color-small-box {
              width: 21px;
              height: 21px;
            }
          }
        }
      }
      .in-cart {
        margin-bottom: 20px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-between;
        padding: 0px;
        gap: 20px;

        @media (max-width: 1280px) {
          margin-bottom: 23px;
        }

        .in-cart__counter {
          padding: 9px 10px;
          gap: 1px;
          display: flex;
          flex-flow: row nowrap;
          align-items: center;
          justify-content: center;
          width: 123px;
          height: 48px;
          background: transparent;
          border: 1px solid $color-2FA7BE;
          border-radius: 6px;
          font-size: 18px;
          font-weight: 500;
          line-height: 23.49px;
          letter-spacing: 0.04em;
          text-align: center;
          color: $color-555A5E;

          .in-cart__counter-btn {
            padding: 5px 10px 8px;
          }
          .in-cart__counter-btn_minus {
          }
          .btn-reset {
          }
          .in-cart__counter-value {
            margin-bottom: 2px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 58px;
            // font-size: 14px;
            // font-weight: 500;
            // line-height: 18.27px;
            // letter-spacing: 0.04em;
            text-align: center;
          }
          .in-cart__counter-btn_plus {
          }
        }
        .in-cart__price-wrap {
          margin-bottom: 7px;

          @media (max-width: 1280px) {
            margin-bottom: 0;
          }

          .in-cart__price {
            font-size: 20px;
            font-weight: 600;
            line-height: 24.46px;
            text-align: left;
          }
          .in-cart__price-desc {
            font-size: 16px;
            font-weight: 500;
            line-height: 19.57px;
            text-align: left;
          }
        }
      }

      .cost {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: flex-start;
        gap: 15px;
        margin-bottom: -4px;

        .cost__cost-value {
          font-size: 28px;
          font-weight: 600;
          line-height: 34.24px;
          text-align: left;
          color: $color-555A5E;

          @media (max-width: 1280px) {
            font-family: Commissioner;
            font-size: 20px;
            font-weight: 600;
            line-height: 24.46px;
            text-align: left;
          }

          &_desc {
            margin-left: -10px;

            @media (max-width: 1280px) {
              font-family: Commissioner;
              font-size: 20px;
              font-weight: 600;
              line-height: 24.46px;
              text-align: left;
            }
          }
        }
      }
      .goods-item__btn-wrap {
        margin-bottom: 20px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: center;
        gap: 14px;

        @media (max-width: 1280px) {
          flex-flow: column nowrap;
          align-items: flex-start;
          padding-top: 7px;
        }

        @media (max-width: 750px) {
          // flex-flow: row nowrap;
          // align-items: flex-start;
          // gap: 4%;
        }

        .btn_montage {
          width: 223px;
          height: 48px;

          @media (max-width: 750px) {
            // flex: 1 1 48%;
            // justify-content: center;
            // width: auto;
          }
        }

        .goods-item__btn_to-cart {
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 10px 20px;
          gap: 10px;
          width: 176px;
          height: 48px;
          background: #2fa7be;
          color: white;
          border-radius: 6px;
          border: 1px solid $color-2FA7BE;
          transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

          @media (max-width: 1280px) {
            width: 223px;
            justify-content: center;
          }

          @media (max-width: 750px) {
            // flex: 1 1 48%;
            // width: auto;

            // justify-content: center;
          }

          .goods-item__btn-text_in-cart {
            display: none;
          }

          &_hide {
            display: none;
          }

          & svg path {
            fill: transparent;
            transition: stroke 0.3s ease-in-out, fill 0.3s ease-in-out;
          }

          &:hover {
            background-color: $color-37CBD3;
            border-color: $color-37CBD3;
          }

          &_active {
            background-color: transparent;
            color: $color-2FA7BE;
            border-color: $color-2FA7BE;

            & svg path {
              stroke: $color-2FA7BE;
              fill: $color-2FA7BE;
            }

            &:hover {
              color: $color-37CBD3;
              border-color: $color-37CBD3;
              background-color: transparent;

              & svg path {
                stroke: $color-37CBD3;
                fill: $color-37CBD3;
              }
            }

            .goods-item__btn-text_no-cart {
              display: none;
            }
            .goods-item__btn-text_in-cart {
              display: block;
            }
          }
        }
      }
      .goods-item__bot-text-wrap {
        margin-bottom: -5px;
        display: flex;
        flex-flow: column nowrap;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 5px;

        @media (max-width: 1280px) {
          position: absolute;
          bottom: -72px;
          left: -127%;
          width: 76vw;
        }

        @media (max-width: 750px) {
          position: relative;
          bottom: unset;
          left: unset;
          width: 100%;
        }
      }
      .goods-item__bot-text {
        width: 571px;
        font-size: 14px;
        font-weight: 500;
        line-height: 17.12px;
        text-align: left;
        color: $color-34343B;

        @media (max-width: 1280px) {
          width: auto;
        }

        &_desc {
          width: 400px;

          @media (max-width: 1280px) {
            position: absolute;
            bottom: -126px;
            left: -127%;
            width: 60vw;
          }

          @media (max-width: 750px) {
            position: relative;
            bottom: unset;
            left: unset;
            width: 100%;
          }
        }
      }
    }
    .bot-block {
      max-width: 579px;
      display: flex;
      flex-flow: column nowrap;
      align-items: flex-start;
      justify-content: flex-start;
      gap: 26px;

      @media (max-width: 1280px) {
        padding-top: 63px;
        // min-height: 600px;
      }

      @media (max-width: 750px) {
        padding-top: 40px;
        // min-height: 600px;
      }

      .bot-block__menu-list {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: flex-start;
        gap: 27px;
      }
      .bot-block__menu-item {
        font-size: 16px;
        font-weight: 500;
        line-height: 19.57px;
        text-align: left;
        color: #898994;
        cursor: pointer;

        &_active {
          font-weight: 700;
          color: $color-2FA7BE;
        }
      }
      .bot-block__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 19.57px;
        text-align: left;
        color: $color-555A5E;
        display: none;

        &_active {
          display: block;
        }
      }
    }
  }
}

